<template>

  <div>
  <el-carousel :interval="3000"
               arrow="always"
               height="400px"
               trigger="click"
               ref="carousel"
               @mouseenter.native="delHandleMouseEnter">
    <el-carousel-item v-for="item in banner" :key="item.id">
      <el-image
          style="width: 100%;height:100%"
          :src="item.src"
          ref="imgHeight"
          oncontextmenu="return false"
          ondragstart="return false"
          fit="cover">
      </el-image>
    </el-carousel-item>
  </el-carousel>
  </div>
</template>

<script>
export default {

  data() {
    return {
      banner:[
      ],
    };
  },
  mounted(){
    this.$refs.carousel.handleMouseEnter = () => { };
  },
  methods: {
    delHandleMouseEnter() {
      this.$refs.carousel.handleMouseEnter = () => { };
    },
    getBannerUrl(){
      this.$http({
        url:"http://rap2api.taobao.org/app/mock/309060/banner",
        method:'get',
      }).then(res=>{
        if (res.data.code=200){
          this.banner=res.data.data.banner
        }
      })
    },
  },
  created() {
    this.getBannerUrl()
  }
}
</script>

<style scoped>
>>>.el-carousel__button {
  width: 15px;
  height: 15px;
  border-radius: 10px;
}

>>>.el-carousel__indicator.is-active button {
  background: #F5A625;
  width: 30px;
}

</style>

